'use client';

import { useState, useEffect } from 'react';
import { useAdminApi } from '@/hooks/useApi';
import { useDesignSystem, getAdminPanelColorsWithDesignSystem } from '@/hooks/useDesignSystem';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { 
  Plus, 
  Edit, 
  Trash2, 
  Building2, 
  Phone, 
  Mail, 
  MapPin,
  Search,
  X,
  Save,
  Users
} from 'lucide-react';

interface Client {
  id: number;
  name: string;
  officeAddress?: string;
  phone?: string;
  email?: string;
  isActive: boolean;
  createdAt: string;
  updatedAt: string;
  projects: Array<{
    id: number;
    projectName: string;
  }>;
}

export default function ClientManager() {
  const { designSystem } = useDesignSystem();
  const colors = getAdminPanelColorsWithDesignSystem(designSystem);
  const { get, post, put, delete: del } = useAdminApi();

  const [clients, setClients] = useState<Client[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [showForm, setShowForm] = useState(false);
  const [editingClient, setEditingClient] = useState<Client | null>(null);
  const [formData, setFormData] = useState<Partial<Client>>({
    name: '',
    officeAddress: '',
    phone: '',
    email: '',
    isActive: true,
  });

  useEffect(() => {
    fetchClients();
  }, []);

  const fetchClients = async () => {
    try {
      setLoading(true);
      const response = await get<{ success: boolean; data: Client[] }>('/api/admin/clients');
      if (response.success) {
        setClients(response.data);
      }
    } catch (error) {
      console.error('Error fetching clients:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const clientData = {
        ...formData,
        email: formData.email || undefined,
        phone: formData.phone || undefined,
        officeAddress: formData.officeAddress || undefined,
      };

      if (editingClient) {
        const response = await put<{ success: boolean; data: Client }>(`/api/admin/clients/${editingClient.id}`, clientData);
        if (response.success) {
          setClients(clients.map(c => c.id === editingClient.id ? response.data : c));
        }
      } else {
        const response = await post<{ success: boolean; data: Client }>('/api/admin/clients', clientData);
        if (response.success) {
          setClients([response.data, ...clients]);
        }
      }

      setShowForm(false);
      setEditingClient(null);
      setFormData({
        name: '',
        officeAddress: '',
        phone: '',
        email: '',
        isActive: true,
      });
    } catch (error) {
      console.error('Error saving client:', error);
    }
  };

  const handleEdit = (client: Client) => {
    setEditingClient(client);
    setFormData({
      name: client.name,
      officeAddress: client.officeAddress || '',
      phone: client.phone || '',
      email: client.email || '',
      isActive: client.isActive,
    });
    setShowForm(true);
  };

  const handleDelete = async (id: number) => {
    if (confirm('Are you sure you want to delete this client?')) {
      try {
        const response = await del(`/api/admin/clients/${id}`) as { success: boolean };
        if (response.success) {
          setClients(clients.filter(c => c.id !== id));
        }
      } catch (error) {
        console.error('Error deleting client:', error);
      }
    }
  };

  const filteredClients = clients.filter(client =>
    client.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    client.email?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    client.officeAddress?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  if (loading) {
    return (
      <div className="flex items-center justify-center p-8">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2" style={{ borderColor: colors.primary }}></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold" style={{ color: colors.textPrimary }}>
            Client Management
          </h1>
          <p className="text-sm" style={{ color: colors.textSecondary }}>
            Manage your clients and their information
          </p>
        </div>
        <Button
          onClick={() => setShowForm(true)}
          className="flex items-center space-x-2"
          style={{ backgroundColor: colors.primary, color: colors.backgroundPrimary }}
        >
          <Plus className="w-4 h-4" />
          <span>Add Client</span>
        </Button>
      </div>

      {/* Search */}
      <div className="relative">
        <Search className="w-4 h-4 absolute left-3 top-1/2 transform -translate-y-1/2" style={{ color: colors.textMuted }} />
        <Input
          type="text"
          placeholder="Search clients..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="pl-10"
          style={{
            backgroundColor: colors.backgroundSecondary,
            borderColor: colors.grayLight,
            color: colors.textPrimary
          }}
        />
      </div>

      {/* Client Form */}
      {showForm && (
        <Card className="p-6" style={{ backgroundColor: colors.backgroundSecondary }}>
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-lg font-semibold" style={{ color: colors.textPrimary }}>
              {editingClient ? 'Edit Client' : 'Add New Client'}
            </h2>
            <Button
              onClick={() => {
                setShowForm(false);
                setEditingClient(null);
                setFormData({
                  name: '',
                  officeAddress: '',
                  phone: '',
                  email: '',
                  isActive: true,
                });
              }}
              variant="ghost"
              className="p-2"
            >
              <X className="w-4 h-4" />
            </Button>
          </div>

          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium mb-2" style={{ color: colors.textPrimary }}>
                  Client Name *
                </label>
                <Input
                  type="text"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  required
                  style={{
                    backgroundColor: colors.backgroundPrimary,
                    borderColor: colors.grayLight,
                    color: colors.textPrimary
                  }}
                />
              </div>

              <div>
                <label className="block text-sm font-medium mb-2" style={{ color: colors.textPrimary }}>
                  Email
                </label>
                <Input
                  type="email"
                  value={formData.email}
                  onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                  style={{
                    backgroundColor: colors.backgroundPrimary,
                    borderColor: colors.grayLight,
                    color: colors.textPrimary
                  }}
                />
              </div>

              <div>
                <label className="block text-sm font-medium mb-2" style={{ color: colors.textPrimary }}>
                  Phone
                </label>
                <Input
                  type="tel"
                  value={formData.phone}
                  onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                  style={{
                    backgroundColor: colors.backgroundPrimary,
                    borderColor: colors.grayLight,
                    color: colors.textPrimary
                  }}
                />
              </div>

              <div className="md:col-span-2">
                <label className="block text-sm font-medium mb-2" style={{ color: colors.textPrimary }}>
                  Office Address
                </label>
                <textarea
                  value={formData.officeAddress}
                  onChange={(e) => setFormData({ ...formData, officeAddress: e.target.value })}
                  rows={3}
                  className="w-full p-3 rounded-lg border resize-none"
                  style={{
                    backgroundColor: colors.backgroundPrimary,
                    borderColor: colors.grayLight,
                    color: colors.textPrimary
                  }}
                />
              </div>

              <div>
                <label className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    checked={formData.isActive}
                    onChange={(e) => setFormData({ ...formData, isActive: e.target.checked })}
                    className="rounded"
                  />
                  <span className="text-sm font-medium" style={{ color: colors.textPrimary }}>
                    Active Client
                  </span>
                </label>
              </div>
            </div>

            <div className="flex items-center space-x-3 pt-4">
              <Button
                type="submit"
                className="flex items-center space-x-2"
                style={{ backgroundColor: colors.primary, color: colors.backgroundPrimary }}
              >
                <Save className="w-4 h-4" />
                <span>{editingClient ? 'Update Client' : 'Create Client'}</span>
              </Button>
              <Button
                type="button"
                onClick={() => {
                  setShowForm(false);
                  setEditingClient(null);
                }}
                variant="ghost"
                style={{ color: colors.textSecondary }}
              >
                Cancel
              </Button>
            </div>
          </form>
        </Card>
      )}

      {/* Clients List */}
      <div className="space-y-4">
        {filteredClients.map((client) => (
          <Card key={client.id} className="p-6" style={{ backgroundColor: colors.backgroundSecondary }}>
            <div className="flex items-start justify-between">
              <div className="flex-1">
                <div className="flex items-center space-x-3 mb-3">
                  <Building2 className="w-5 h-5" style={{ color: colors.primary }} />
                  <h3 className="text-lg font-semibold" style={{ color: colors.textPrimary }}>
                    {client.name}
                  </h3>
                  {!client.isActive && (
                    <span 
                      className="px-2 py-1 text-xs rounded-full"
                      style={{ backgroundColor: colors.error, color: colors.backgroundPrimary }}
                    >
                      Inactive
                    </span>
                  )}
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm mb-4">
                  {client.email && (
                    <div className="flex items-center space-x-2">
                      <Mail className="w-4 h-4" style={{ color: colors.textMuted }} />
                      <span style={{ color: colors.textSecondary }}>Email:</span>
                      <span style={{ color: colors.textPrimary }}>{client.email}</span>
                    </div>
                  )}

                  {client.phone && (
                    <div className="flex items-center space-x-2">
                      <Phone className="w-4 h-4" style={{ color: colors.textMuted }} />
                      <span style={{ color: colors.textSecondary }}>Phone:</span>
                      <span style={{ color: colors.textPrimary }}>{client.phone}</span>
                    </div>
                  )}

                  {client.officeAddress && (
                    <div className="flex items-center space-x-2 md:col-span-2">
                      <MapPin className="w-4 h-4" style={{ color: colors.textMuted }} />
                      <span style={{ color: colors.textSecondary }}>Address:</span>
                      <span style={{ color: colors.textPrimary }}>{client.officeAddress}</span>
                    </div>
                  )}
                </div>

                {client.projects.length > 0 && (
                  <div className="flex items-center space-x-2 text-sm">
                    <Users className="w-4 h-4" style={{ color: colors.textMuted }} />
                    <span style={{ color: colors.textSecondary }}>
                      {client.projects.length} project{client.projects.length !== 1 ? 's' : ''}:
                    </span>
                    <div className="flex flex-wrap gap-1">
                      {client.projects.map((project, index) => (
                        <span key={project.id} className="text-xs">
                          <span style={{ color: colors.textPrimary }}>{project.projectName}</span>
                          {index < client.projects.length - 1 && (
                            <span style={{ color: colors.textMuted }}>, </span>
                          )}
                        </span>
                      ))}
                    </div>
                  </div>
                )}
              </div>

              <div className="flex items-center space-x-2 ml-4">
                <Button
                  onClick={() => handleEdit(client)}
                  variant="ghost"
                  className="p-2"
                  style={{ color: colors.primary }}
                >
                  <Edit className="w-4 h-4" />
                </Button>
                <Button
                  onClick={() => handleDelete(client.id)}
                  variant="ghost"
                  className="p-2"
                  style={{ color: colors.error }}
                >
                  <Trash2 className="w-4 h-4" />
                </Button>
              </div>
            </div>
          </Card>
        ))}
      </div>

      {filteredClients.length === 0 && (
        <Card className="p-8 text-center" style={{ backgroundColor: colors.backgroundSecondary }}>
          <Building2 className="w-12 h-12 mx-auto mb-4" style={{ color: colors.textMuted }} />
          <h3 className="text-lg font-semibold mb-2" style={{ color: colors.textPrimary }}>
            No clients found
          </h3>
          <p className="text-sm" style={{ color: colors.textSecondary }}>
            {searchTerm ? 'Try adjusting your search terms' : 'Get started by adding your first client'}
          </p>
        </Card>
      )}
    </div>
  );
}
